.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 1rem;
}

.modal-container {
  background: #ffffff;
  border-radius: 12px;
  width: 100%;
  max-width: 900px;
  max-height: 90vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
}

.modal-header {
  padding: 1.5rem 1.5rem 1rem;
  border-bottom: 1px solid #e5e7eb;
  position: relative;
}

.modal-title {
  font-size: 1.5rem;
  font-weight: 600;
  color: #111827;
  margin: 0 0 0.25rem 0;
}

.modal-subtitle {
  color: #6b7280;
  font-size: 0.875rem;
  margin: 0;
}

.close-btn {
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
  background: none;
  border: none;
  color: #6b7280;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 6px;
  transition: all 0.2s;
}
.close-btn:hover {
  background-color: #f3f4f6;
  color: #374151;
}
.close-btn svg {
  width: 1.25rem;
  height: 1.25rem;
}

.modal-body {
  flex: 1;
  overflow-y: auto;
  padding: 1.5rem;
}

.form-section {
  margin-bottom: 2rem;
}
.form-section:last-child {
  margin-bottom: 0;
}

.section-header {
  display: flex;
  align-items: center;
  margin-bottom: 1.5rem;
  gap: 0.75rem;
}

.section-icon {
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ffffff;
}
.section-icon.basic {
  background: linear-gradient(135deg, #3b82f6, #1d4ed8);
}
.section-icon.contact {
  background: linear-gradient(135deg, #10b981, #059669);
}
.section-icon.address {
  background: linear-gradient(135deg, #f59e0b, #d97706);
}
.section-icon.business {
  background: linear-gradient(135deg, #8b5cf6, #7c3aed);
}
.section-icon.notes {
  background: linear-gradient(135deg, #ef4444, #dc2626);
}
.section-icon svg {
  width: 1.25rem;
  height: 1.25rem;
}

.section-title {
  font-size: 1.125rem;
  font-weight: 600;
  color: #111827;
  margin: 0;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  margin-bottom: 1rem;
}
.form-row:last-child {
  margin-bottom: 0;
}
.form-row .form-field.full-width {
  grid-column: 1/-1;
}

.form-field {
  display: flex;
  flex-direction: column;
}

.field-label {
  font-size: 0.875rem;
  font-weight: 500;
  color: #374151;
  margin-bottom: 0.5rem;
}
.field-label::after {
  content: " *";
  color: #ef4444;
}
.field-label:not(:has(+ .form-input[required]))::after {
  content: "";
}

.form-input,
.form-select,
.form-textarea {
  background-color: #ffffff;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  padding: 0.75rem;
  color: #111827;
  font-size: 0.875rem;
  transition: all 0.2s;
}
.form-input:focus,
.form-select:focus,
.form-textarea:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}
.form-input::placeholder,
.form-select::placeholder,
.form-textarea::placeholder {
  color: #9ca3af;
}
.form-input:readonly,
.form-select:readonly,
.form-textarea:readonly {
  background-color: #f9fafb;
  color: #6b7280;
}

.form-textarea {
  resize: vertical;
  min-height: 80px;
}

.input-with-button {
  display: flex;
  gap: 0.5rem;
}

.generate-btn {
  background-color: #3b82f6;
  color: #ffffff;
  border: none;
  padding: 0.75rem 1rem;
  border-radius: 6px;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
  white-space: nowrap;
}
.generate-btn:hover {
  background-color: #2563eb;
}
.generate-btn:active {
  transform: translateY(1px);
}

.input-with-icon {
  position: relative;
}
.input-with-icon .input-icon {
  position: absolute;
  left: 0.75rem;
  top: 50%;
  transform: translateY(-50%);
  width: 1.25rem;
  height: 1.25rem;
  color: #6b7280;
}
.input-with-icon .form-input {
  padding-left: 2.75rem;
}

.error-message {
  color: #ef4444;
  font-size: 0.75rem;
  margin-top: 0.25rem;
}

.error-section {
  margin-top: 1rem;
  padding: 1rem;
  background-color: #fef2f2;
  border: 1px solid #fecaca;
  border-radius: 6px;
}
.error-section .error-message {
  color: #ef4444;
  font-size: 0.875rem;
  margin: 0;
}

.modal-footer {
  padding: 1rem 1.5rem 1.5rem;
  border-top: 1px solid #e5e7eb;
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
}

.btn {
  padding: 0.75rem 1.5rem;
  border-radius: 6px;
  font-size: 0.875rem;
  font-weight: 500;
  border: none;
  cursor: pointer;
  transition: all 0.2s;
  min-width: 100px;
}
.btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.btn.btn-primary {
  background-color: #3b82f6;
  color: #ffffff;
}
.btn.btn-primary:hover:not(:disabled) {
  background-color: #2563eb;
}
.btn.btn-secondary {
  background-color: #6b7280;
  color: #ffffff;
}
.btn.btn-secondary:hover:not(:disabled) {
  background-color: #4b5563;
}

@media (max-width: 768px) {
  .modal-container {
    max-width: 100%;
    margin: 0 1rem;
  }
  .form-row {
    grid-template-columns: 1fr;
  }
  .modal-header {
    padding: 1rem;
  }
  .modal-body {
    padding: 1rem;
  }
  .modal-footer {
    padding: 1rem;
    flex-direction: column;
  }
  .modal-footer .btn {
    width: 100%;
  }
}
